import React from 'react';
import { CustomTariff } from './CustomTariff';
import { RegularTariffList } from './RegularTariffList';
import clsx from 'clsx';
import { ImArrowRight2 } from 'react-icons/im';
import EntityContainer from 'modules/entity/containers';
import { useAppState } from '../../../shared/state';
import { PlanType } from '../types';
import { Loader } from '../../../shared/components';
import { useRouter } from '../../../shared/hooks';
import { useSubscription } from '../hooks';

export function TariffPlans() {
	const { companyId } = useAppState();
	const { history } = useRouter();
	const { hasUserAlreadyPaidInvoice } = useSubscription();

	const navigateToCheckout = () => history.push('/checkout');
	return (
		<>
			<EntityContainer.All
				entity="plans"
				name="AllPlan"
				url={`/${companyId}/payment/plans/`}
				params={{
					limit: 10
				}}
			>
				{({ items, isFetched }) => {
					const planItems = items as PlanType[];
					return (
						<div className="relative inline-flex flex-col items-end" id="plans-description">
							{isFetched && items.length ? (
								<div className="mb-6 flex">
									<CustomTariff planItems={planItems} />
									<RegularTariffList planItems={planItems} />
								</div>
							) : (
								<Loader tiled withOverlay />
							)}
							<button
								className={clsx(
									'h-8  rounded-full border border-primary-dark leading-8' +
										' inline-flex items-center justify-between  text-13px font-medium transition' +
										' bg-primary-dark px-6 text-white hover:bg-white hover:text-primary-dark '
								)}
								onClick={navigateToCheckout}
							>
								<span className="mr-24">{hasUserAlreadyPaidInvoice ? 'Upgrade' : 'Sign Up'}</span>
								<ImArrowRight2 />
							</button>
						</div>
					);
				}}
			</EntityContainer.All>
		</>
	);
}
